<template>
  <div class="main">
    <div class="choice-btns">
      <v-btn
        flat
        @click="changeOper('plusActive')"
        class="btn-oper plus"
        :class="{ 'cyan darken-4': state.plusActive }"
      ></v-btn>
      <v-btn
        flat
        @click="changeOper('minusActive')"
        class="btn-oper minus"
        :class="{ 'cyan darken-4': state.minusActive }"
      ></v-btn>
      <v-btn
        flat
        @click="changeOper('multyActive')"
        class="btn-oper multy"
        :class="{ 'cyan darken-4': state.multyActive }"
      ></v-btn>
      <v-btn
        flat
        disabled
        @click="changeOper('divActive')"
        class="disabled btn-oper div"
        :class="{ 'cyan darken-4': state.divActive }"
      ></v-btn>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      state: {
        plusActive: true,
        minusActive: false,
        multyActive: false,
        divActive: false
      }
    };
  },
  methods: {
    changeOper(oper) {
      Object.keys(this.state).forEach(key => {
        this.state[key] = false;
      });
      this.state[oper] = true;
      let curOper;
      switch (oper) {
        case "plusActive":
          curOper = "plus";
          break;
        case "minusActive":
          curOper = "minus";
          break;
        case "multyActive":
          curOper = "multy";
          break;
        case "divActive":
          curOper = "div";
          break;
      }
      this.$emit("operchange", curOper);
    }
  }
};
</script>

