<template>
  <div id="app">
    <v-app dark>
      <div v-show="infoShow" class="overlay"></div>
      <Navigation
        @openInfoModal="infoShow = !infoShow"
        @toogleContent="show = !show"
        @refresh="refresh"
      ></Navigation>
      <div class="progress-wrap">
        <ProgressBar :wrong="wrongCounter" :right="rightCounter" />
      </div>
      <div class="oper-wrap" :class="{ show: show }">
        <OperPick @operchange="operChange" />
      </div>
      <div class="mian-wrap">
        <Main
          :curoper="oper"
          :firstmin="firstNumber.min"
          :firstmax="firstNumber.max"
          :secondmin="secondNumber.min"
          :secondmax="secondNumber.max"
          @scoresIncrement="progressChange"
        />
      </div>
      <div class="range-wrap" :class="{ show: show }">
        <RangePick
          title="First number range pick"
          @changecurlimits="firstNumberChangeLimits"
        />
        <RangePick
          title="Second number range pick"
          @changecurlimits="secondNumberChangeLimits"
        />
      </div>

      <transition name="slide-fade">
        <InfoModal v-show="infoShow" @closewindow="infoShow = false" />
      </transition>
    </v-app>
  </div>
</template>
<script>
import ProgressBar from "./components/ProgressBar.vue";
import OperPick from "./components/OperPick.vue";
import Main from "./components/Main.vue";
import RangePick from "./components/RangePick.vue";
import InfoModal from "./components/InfoModal.vue";
import Navigation from "./components/Navigation.vue";
export default {
  name: "app",
  components: {
    ProgressBar,
    OperPick,
    Main,
    RangePick,
    InfoModal,
    Navigation
  },
  data() {
    return {
      show: false,
      infoShow: false,

      oper: "plus",

      wrongCounter: 0,
      rightCounter: 0,

      firstNumber: {
        min: 0,
        max: 400
      },
      secondNumber: {
        min: 0,
        max: 400
      }
    };
  },
  methods: {
    test(arg) {
      console.log(arg, "asdasd");
    },
    operChange(op) {
      this.oper = op;
    },
    firstNumberChangeLimits(lims) {
      console.log(`first range changed`, lims);
      this.firstNumber.min = lims.min;
      this.firstNumber.max = lims.max;
    },
    secondNumberChangeLimits(lims) {
      console.log(`second range changed`, lims);
      this.secondNumber.min = lims.min;
      this.secondNumber.max = lims.max;
    },
    progressChange(type) {
      if (type) this.rightCounter++;
      else this.wrongCounter++;
    },
    refresh() {
      this.wrongCounter = 0;
      this.rightCounter = 0;
    }
  }
};
</script>

