<template>
  <v-card flat color="transparent">
    <span v-if="helpInfo" class="title">
      <v-subheader>{{ title }}</v-subheader>
    </span>
    <v-card-text>
      <v-layout row>
        <v-flex shrink style="width: 60px">
          <v-text-field
            v-model="limits[0]"
            class="mt-0"
            hide-details
            single-line
            type="number"
          ></v-text-field>
        </v-flex>

        <v-flex class="px-3 ">
          <v-range-slider
            @change="rangeChange"
            v-model="value"
            :max="limits[1]"
            :min="limits[0]"
            :step="1"
            thumb-label="always"
            :color="color"
            persistent-hint
          ></v-range-slider>
        </v-flex>

        <v-flex shrink center style=" width: 60px;">
          <v-text-field
            v-model="limits[1]"
            class="mt-0"
            hide-details
            single-line
            type="number"
          ></v-text-field>
        </v-flex>
      </v-layout>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  props: {
    title: String
  },
  data() {
    return {
      value: [0, 400],
      limits: [0, 1000],
      color: "#53A13C",
      helpInfo: true
    };
  },
  methods: {
    rangeChange() {
      this.helpInfo = false;
      this.$emit("changecurlimits", {
        min: this.value[0],
        max: this.value[1]
      });
    }
  },
  watch: {
    limits: {
      handler() {
        this.limits[0] = +this.limits[0];
        this.limits[1] = +this.limits[1];
        if (this.limits[0] === this.limits[1]) {
          this.limits[1] = +this.limits[0] + 1;
        }

        [this.value[0], this.value[1]] = [this.limits[0], this.limits[1]];
        this.rangeChange();
      },
      deep: true
    }
  }
};
</script>

<style scoped lang="scss">
.title {
  position: absolute;
  left: 50%;
  bottom: 0;
  z-index: 0;
  opacity: 0.4;
  transform: translateX(-50%);
  text-align: center;
}
</style>
