function compareObjects(obj1, obj2) {
  return JSON.stringify(obj1) === JSON.stringify(obj2);
}

export default class {
  constructor() {
    this.limitsStore = new Map();
    this.numbersHash = new Map();
  }

  randInt(min, max) {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  limitsChangeCheck(limits, numId) {
    if (!this.limitsStore.has(numId)) return true;
    return !compareObjects(this.limitsStore.get(numId), limits);
  }

  limitsRanOutCheck(limits, numId) {
    const maxOptions = limits[1] - limits[0] - 1;
    const hashSize = this.numbersHash.get(numId).size;
    return hashSize >= maxOptions;
  }

  createOrClearHash(numId) {
    if (this.numbersHash.has(numId)) {
      this.numbersHash.get(numId).clear();
    } else {
      this.numbersHash.set(numId, new Set());
    }
  }

  setlimits(numId, limits) {
    this.limitsStore.set(numId, limits);
  }

  pushInHahs(numId, value) {
    const currentHash = this.numbersHash.get(numId);
    currentHash.add(value);
    this.numbersHash.set(numId, currentHash);
  }

  randUnique(numId, limits) {
    let number = this.randInt(limits[0], limits[1]);
    while (this.numbersHash.get(numId).has(number)) {
      number = this.randInt(limits[0], limits[1]);
    }
    return number;
  }

  get(numId, limits) {
    let number;
    if (
      this.limitsChangeCheck(limits, numId) ||
      this.limitsRanOutCheck(limits, numId)
    ) {
      this.createOrClearHash(numId);
    }
    this.setlimits(numId, limits);

    console.log(numId, this.limitsRanOutCheck(limits, numId));
    console.log(this.numbersHash);

    number = this.randUnique(numId, limits);
    this.pushInHahs(numId, number);
    return number;
  }
}
